import React,{ useContext, useState } from 'react';
import { Threndcontext } from "../Context/Context";


function Thrend() {

  const { thrends, addthrend } = useContext(Threndcontext);

  const [newThrend, setNewThrend] = useState()

  return (
    <div>
      {thrends.map(thrend=>(
      <div>
      <p>{thrend.title}</p>
      <p>{thrend.content}</p>
      </div>
    ))}
    <form>
      <input type="text" placeholder="title"/>
      <input type="text" placeholder="content"/>
      <input 
      onClick={()=>addthrend(newThrend)}
      type="submit" 
      value="add thrend"/>
    </form>
    </div>
  )
}

export default Thrend;
